.gradient-wrapper-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(255, 200, 200, 0.4), rgba(255, 150, 150, 0.2), rgba(255, 100, 100, 0));
  border-radius: 50%;
  padding: 20px;
  box-shadow: 0 0 20px rgba(255, 100, 100, 0.2);
}
#grad1 {
  border-style: none;
  border-radius: 50%;
  background-color: #1ED760;
  justify-content: center;
  box-shadow: 0 0 100px #1ED760;
}
.center-logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  position: absolute;
}
.logo-header {
  margin-top: 20px;
  padding-bottom: 10px;
  border-bottom: 2px solid #1ED760;
}
.logo-row {
  width: 250px;
  margin: 0 auto;
}
.logo {
  width: 100%;
  height: 150px;
  text-align: center;
